
.btn-outline {
  display: flex;
  column-gap: 10px;
  align-items: center;
  height: 48px;
  padding: 12px 20px;
  border-radius: 5px;
  border: 1px solid var(--blackpure);
  background-color: var(--white);
  color: var(--blackpure);
  transition: opacity 0.2s ease-in;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    position: relative;
    top: 1px;
  }
}

.btn {
  display: flex;
  column-gap: 10px;
  align-items: center;

  & img {
    height: 20px;
  }
}